.sidebar { 
    height: 100vh;
    flex: 0.2;
    color: white;
    padding-left: 10px;
    padding-right: 10px;
    min-width: 230px;
    height: 100vh;
    overflow-y: overlay;
    background-color: #000000;
}

.sidebar::-webkit-scrollbar {
    display: none;
}

.sidebar__logo {
    margin-right: auto;
    height: 70px;
    padding: 10px;
}

.sidebar__title {
    margin-left: 10px;
    padding-top: 5px;
    font-size: 12px;
}